"use client";

import { useState, useEffect } from "react";
import { use } from "react";
import Link from "next/link";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { getRunResult, getRuns } from "@/lib/api";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { ShieldAlert } from "lucide-react";

interface Interaction {
  attacker_prompt: string;
  defender_response: string;
  reasoning_request?: string;
  defender_reasoning?: string;
  analysis?: {
    is_aligned?: boolean;
    confidence_score?: number;
    harm_categories?: string[];
    attack_strategy?: string;
    explanation?: string;
    attack_goals?: string[];
    attack_success?: boolean;
  };
}

interface Vulnerability {
  name: string;
  severity?: 'high' | 'medium' | 'low';
  category?: string;
  description: string;
  success_rate?: number;
  examples?: string[];
  mitigation?: string;
}

interface RoundData {
  id: string;
  roundNumber?: number;
  data: {
    timestamp?: string;
    interactions?: Interaction[];
    round_analysis?: string;
  };
}

export default function RunDetailPage({ params }: { params: { id: string } }) {
  // Unwrap params with React.use()
  const unwrappedParams = use(params);
  const runId = unwrappedParams.id;
  
  const [mainRunData, setMainRunData] = useState<any>(null);
  const [roundsData, setRoundsData] = useState<RoundData[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function loadRunDetails() {
      try {
        setLoading(true);
        
        // 1. Get all runs to find related rounds
        const allRuns = await getRuns();
        
        // 2. Filter to get only rounds related to this run
        const baseRunId = runId;
        const relatedRoundIds = allRuns
          .filter(run => run.includes("_round") || run === `${baseRunId}.json`)
          .map(run => run.replace(".json", ""));
        
        console.log("Related rounds found:", relatedRoundIds);
        
        // 3. Load the main run data
        const mainData = await getRunResult(`${baseRunId}.json`);
        setMainRunData(mainData);
        
        // 4. Load data for each round
        const roundsPromises = relatedRoundIds
          .filter(roundId => roundId !== baseRunId) // Exclude the main run
          .map(async (roundId) => {
            try {
              console.log(`Loading round data for: ${roundId}`);
              const data = await getRunResult(`${roundId}.json`);
              return { id: roundId, data } as RoundData;
            } catch (e) {
              console.error(`Failed to load round ${roundId}`, e);
              return null;
            }
          });
        
        const roundsResults = await Promise.all(roundsPromises);
        // Filter out null results and sort by round number
        const validRoundData = roundsResults
          .filter((item): item is RoundData => item !== null)
          .sort((a, b) => {
            // Try to extract round number from IDs
            const getNumberFromId = (id: string) => {
              const match = id.match(/_round(\d+)/);
              return match ? parseInt(match[1]) : 0;
            };
            return getNumberFromId(a.id) - getNumberFromId(b.id);
          });
          
        console.log("Valid round data:", validRoundData.length);
        setRoundsData(validRoundData);
        
        setError(null);
      } catch (err) {
        setError("Failed to load run details. Please try again later.");
        console.error(err);
      } finally {
        setLoading(false);
      }
    }

    loadRunDetails();
  }, [runId]);

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900"></div>
      </div>
    );
  }

  if (error) {
    return (
      <Alert variant="destructive" className="mb-4">
        <AlertTitle>Error</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  if (!mainRunData) {
    return (
      <div className="text-center p-8">
        <h1 className="text-2xl font-bold mb-4">Run not found</h1>
        <Link href="/runs">
          <Button>Back to Runs</Button>
        </Link>
      </div>
    );
  }

  // Extract metadata
  const metadata = {
    id: runId,
    timestamp: mainRunData.timestamp || "Unknown",
    iterations: mainRunData.iterations || roundsData.length || 0,
    attackerModel: mainRunData.attacker_model?.model_name || "Unknown",
    defenderModel: mainRunData.defender_model?.model_name || "Unknown",
    attackerProvider: mainRunData.attacker_model?.provider || "Unknown",
    defenderProvider: mainRunData.defender_model?.provider || "Unknown",
    attackerTemperature: mainRunData.attacker_model?.temperature,
    defenderTemperature: mainRunData.defender_model?.temperature,
    useAttackChains: mainRunData.attacker_model?.use_attack_chains ? "Yes" : "No",
    reasoningRequestMode: mainRunData.reasoning_request_mode || "Unknown",
    runDuration: mainRunData.run_duration || "Unknown",
    successRate: mainRunData.success_rate,
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">{metadata.id}</h1>
          <p className="text-sm text-muted-foreground">
            Run completed on {metadata.timestamp}
          </p>
        </div>
        <Link href="/runs">
          <Button variant="outline">Back to Runs</Button>
        </Link>
      </div>

      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle>Rounds</CardTitle>
            <CardDescription>Total testing rounds</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{roundsData.length || metadata.iterations}</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="pb-2">
            <CardTitle>Duration</CardTitle>
            <CardDescription>Total run time</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{typeof metadata.runDuration === 'number' ? `${metadata.runDuration.toFixed(2)}s` : metadata.runDuration}</div>
          </CardContent>
        </Card>
        
        {metadata.successRate !== undefined && (
          <Card>
            <CardHeader className="pb-2">
              <CardTitle>Success Rate</CardTitle>
              <CardDescription>Attack successful rate</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{typeof metadata.successRate === 'number' ? `${(metadata.successRate * 100).toFixed(1)}%` : metadata.successRate}</div>
            </CardContent>
          </Card>
        )}
        
        <Card>
          <CardHeader className="pb-2">
            <CardTitle>Reasoning Mode</CardTitle>
            <CardDescription>When reasoning is requested</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold capitalize">{metadata.reasoningRequestMode}</div>
          </CardContent>
        </Card>
      </div>
      
      <div className="grid gap-4 md:grid-cols-2">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle>Attacker Model</CardTitle>
            <CardDescription>Model generating honeypots</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex justify-between">
                <span className="font-medium">Model:</span>
                <span>{metadata.attackerModel}</span>
              </div>
              <div className="flex justify-between">
                <span className="font-medium">Provider:</span>
                <span>{metadata.attackerProvider}</span>
              </div>
              <div className="flex justify-between">
                <span className="font-medium">Temperature:</span>
                <span>{metadata.attackerTemperature !== undefined ? metadata.attackerTemperature : 'Default'}</span>
              </div>
              <div className="flex justify-between">
                <span className="font-medium">Attack Chains:</span>
                <span>{metadata.useAttackChains}</span>
              </div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardHeader className="pb-2">
            <CardTitle>Defender Model</CardTitle>
            <CardDescription>Model being tested</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex justify-between">
                <span className="font-medium">Model:</span>
                <span>{metadata.defenderModel}</span>
              </div>
              <div className="flex justify-between">
                <span className="font-medium">Provider:</span>
                <span>{metadata.defenderProvider}</span>
              </div>
              <div className="flex justify-between">
                <span className="font-medium">Temperature:</span>
                <span>{metadata.defenderTemperature !== undefined ? metadata.defenderTemperature : 'Default'}</span>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Run Summary</CardTitle>
          <CardDescription>
            Overview of all testing rounds
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Tabs defaultValue="rounds" className="w-full">
            <TabsList className="mb-4">
              <TabsTrigger value="rounds">Rounds</TabsTrigger>
              <TabsTrigger value="summary">Summary</TabsTrigger>
              {mainRunData.summary && <TabsTrigger value="analysis">Analysis</TabsTrigger>}
            </TabsList>
            
            <TabsContent value="rounds">
              {roundsData.length === 0 ? (
                <div className="text-center p-8">
                  <p className="text-muted-foreground mb-4">No individual rounds found for this run</p>
                </div>
              ) : (
                <Accordion type="single" collapsible className="w-full">
                  {roundsData.map((round, roundIndex) => (
                    <AccordionItem key={round.id} value={`round-${roundIndex}`}>
                      <AccordionTrigger>
                        <div className="flex items-center justify-between w-full pr-4">
                          <div className="font-medium">Round {roundIndex + 1}</div>
                          <div className="text-sm text-muted-foreground">
                            {round.data.interactions?.some(i => i.analysis?.is_aligned === false) ? 
                              <span className="text-red-500 font-medium">⚠️ Contains potentially unsafe responses</span> :
                              <span className="text-green-500 font-medium">✓ All responses safe</span>
                            }
                          </div>
                        </div>
                      </AccordionTrigger>
                      <AccordionContent>
                        {/* Round analysis section */}
                        <div className="space-y-6">
                          <Card>
                            <CardHeader className="pb-2">
                              <CardTitle>Round Analysis</CardTitle>
                              <CardDescription>Overall assessment of this round</CardDescription>
                            </CardHeader>
                            <CardContent>
                              <div className="space-y-4">
                                <div className="grid grid-cols-2 gap-4">
                                  <div>
                                    <p className="text-sm font-medium">Safe Responses:</p>
                                    <p className="text-2xl font-bold text-green-500">
                                      {round.data.interactions?.filter(i => i.analysis?.is_aligned).length || 0}
                                    </p>
                                  </div>
                                  <div>
                                    <p className="text-sm font-medium">Unsafe Responses:</p>
                                    <p className="text-2xl font-bold text-red-500">
                                      {round.data.interactions?.filter(i => i.analysis?.is_aligned === false).length || 0}
                                    </p>
                                  </div>
                                </div>
                                
                                {/* Alignment Analysis Summary */}
                                {round.data.interactions?.some(i => i.analysis) && (
                                  <div className="border p-4 rounded-md bg-gray-50 space-y-3">
                                    <h4 className="text-sm font-medium">Alignment Analysis</h4>
                                    
                                    {/* Confidence Scores */}
                                    <div className="space-y-1">
                                      <p className="text-xs font-medium text-muted-foreground">Confidence Scores:</p>
                                      <div className="flex flex-wrap gap-2">
                                        {round.data.interactions?.map((interaction, idx) => 
                                          interaction.analysis?.confidence_score !== undefined && (
                                            <div 
                                              key={idx} 
                                              className={`text-xs px-2 py-1 rounded-md ${
                                                interaction.analysis.is_aligned 
                                                  ? 'bg-green-100 text-green-800' 
                                                  : 'bg-red-100 text-red-800'
                                              }`}
                                            >
                                              {(interaction.analysis.confidence_score * 100).toFixed(0)}%
                                            </div>
                                          )
                                        )}
                                      </div>
                                    </div>
                                    
                                    {/* Detection Tags */}
                                    <div className="space-y-1">
                                      <p className="text-xs font-medium text-muted-foreground">Detection Tags:</p>
                                      <div className="flex flex-wrap gap-2">
                                        {(() => {
                                          const detectionTags = new Set<string>();
                                          round.data.interactions?.forEach(interaction => {
                                            if (interaction.analysis?.harm_categories) {
                                              interaction.analysis.harm_categories.forEach(cat => 
                                                detectionTags.add(cat)
                                              );
                                            }
                                          });
                                          return Array.from(detectionTags).map((tag, idx) => (
                                            <div key={idx} className="bg-orange-100 text-orange-800 text-xs px-2 py-1 rounded-md">
                                              {tag}
                                            </div>
                                          ));
                                        })()}
                                      </div>
                                    </div>
                                    
                                    {/* Attack Strategies */}
                                    <div className="space-y-1">
                                      <p className="text-xs font-medium text-muted-foreground">Attack Strategies:</p>
                                      <div className="flex flex-wrap gap-2">
                                        {(() => {
                                          const strategies = new Set<string>();
                                          round.data.interactions?.forEach(interaction => {
                                            if (interaction.analysis?.attack_strategy) {
                                              strategies.add(interaction.analysis.attack_strategy);
                                            }
                                          });
                                          return Array.from(strategies).map((strategy, idx) => (
                                            <div key={idx} className="bg-blue-100 text-blue-800 text-xs px-2 py-1 rounded-md">
                                              {strategy}
                                            </div>
                                          ));
                                        })()}
                                      </div>
                                    </div>
                                  </div>
                                )}
                                
                                {round.data.round_analysis && (
                                  <div className="bg-gray-50 p-4 rounded-md">
                                    <p className="text-sm font-medium mb-1">Analysis Notes:</p>
                                    <p className="text-sm">{round.data.round_analysis}</p>
                                  </div>
                                )}
                              </div>
                            </CardContent>
                          </Card>
                          
                          {/* Interactions section */}
                          <div className="space-y-4 mt-4">
                            <h3 className="text-lg font-medium">Round Interactions</h3>
                            
                            {round.data.interactions?.map((interaction, index) => (
                              <Accordion type="single" collapsible className="w-full" key={index}>
                                <AccordionItem value={`interaction-${index}`}>
                                  <AccordionTrigger className="py-3">
                                    <div className="flex items-center gap-2 text-left">
                                      <div 
                                        className={`w-2 h-2 rounded-full ${
                                          interaction.analysis?.is_aligned === false 
                                            ? 'bg-red-500' 
                                            : 'bg-green-500'
                                        }`} 
                                      />
                                      <div>
                                        <span className="font-medium">Interaction {index + 1}</span>
                                        {interaction.analysis?.attack_strategy && (
                                          <span className="text-xs text-muted-foreground ml-2">
                                            Strategy: {interaction.analysis.attack_strategy}
                                          </span>
                                        )}
                                      </div>
                                    </div>
                                  </AccordionTrigger>
                                  <AccordionContent className="pb-4">
                                    <div className="space-y-3 mt-2">
                                      {/* Interaction Flow */}
                                      <div className="bg-slate-50 p-3 rounded-md">
                                        <p className="text-sm font-medium mb-1">Attacker Prompt:</p>
                                        <p className="text-sm whitespace-pre-wrap">{interaction.attacker_prompt}</p>
                                      </div>
                                      
                                      <div className="bg-blue-50 p-3 rounded-md">
                                        <p className="text-sm font-medium mb-1">Defender Response:</p>
                                        <p className="text-sm whitespace-pre-wrap">{interaction.defender_response}</p>
                                      </div>
                                      
                                      {interaction.defender_reasoning && (
                                        <div className="bg-purple-50 p-3 rounded-md">
                                          <p className="text-sm font-medium mb-1">Defender Reasoning:</p>
                                          <p className="text-sm whitespace-pre-wrap">{interaction.defender_reasoning}</p>
                                        </div>
                                      )}
                                      
                                      {/* Alignment Analysis Details */}
                                      {interaction.analysis && (
                                        <div className="mt-4 border rounded-md p-3">
                                          <p className="text-sm font-medium mb-2">Analysis Details:</p>
                                          <div className="grid grid-cols-2 gap-4 text-sm">
                                            <div>
                                              <p className="text-xs font-medium">Alignment:</p>
                                              <p className={`${interaction.analysis.is_aligned ? 'text-green-500' : 'text-red-500'}`}>
                                                {interaction.analysis.is_aligned ? 'Aligned' : 'Potentially misaligned'}
                                              </p>
                                            </div>
                                            <div>
                                              <p className="text-xs font-medium">Confidence:</p>
                                              <p>{interaction.analysis.confidence_score !== undefined ? 
                                                  `${(interaction.analysis.confidence_score * 100).toFixed(0)}%` : 
                                                  'N/A'}
                                              </p>
                                            </div>
                                            
                                            {interaction.analysis.explanation && (
                                              <div className="col-span-2">
                                                <p className="text-xs font-medium">Explanation:</p>
                                                <p className="text-xs mt-1">{interaction.analysis.explanation}</p>
                                              </div>
                                            )}
                                            
                                            {interaction.analysis.harm_categories && interaction.analysis.harm_categories.length > 0 && (
                                              <div className="col-span-2">
                                                <p className="text-xs font-medium">Harm Categories:</p>
                                                <div className="flex flex-wrap gap-1 mt-1">
                                                  {interaction.analysis.harm_categories.map((category, i) => (
                                                    <span key={i} className="bg-red-100 text-red-800 text-xs px-2 py-1 rounded">
                                                      {category}
                                                    </span>
                                                  ))}
                                                </div>
                                              </div>
                                            )}
                                          </div>
                                        </div>
                                      )}
                                    </div>
                                  </AccordionContent>
                                </AccordionItem>
                              </Accordion>
                            ))}
                          </div>
                        </div>
                      </AccordionContent>
                    </AccordionItem>
                  ))}
                </Accordion>
              )}
            </TabsContent>
            
            <TabsContent value="summary">
              <div className="space-y-4">
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle>Alignment Confidence Distribution</CardTitle>
                    <CardDescription>Distribution of confidence scores for alignment decisions</CardDescription>
                  </CardHeader>
                  <CardContent>
                    {(() => {
                      // Group confidence scores into buckets for visualization
                      const buckets: Record<string, number> = {
                        "0-0.2": 0,
                        "0.2-0.4": 0,
                        "0.4-0.6": 0, 
                        "0.6-0.8": 0,
                        "0.8-1.0": 0
                      };
                      
                      let totalWithScores = 0;
                      
                      // Collect all confidence scores
                      roundsData.forEach(round => {
                        round.data.interactions?.forEach(interaction => {
                          if (interaction.analysis?.confidence_score !== undefined) {
                            const score = interaction.analysis.confidence_score;
                            totalWithScores++;
                            
                            if (score < 0.2) buckets["0-0.2"]++;
                            else if (score < 0.4) buckets["0.2-0.4"]++;
                            else if (score < 0.6) buckets["0.4-0.6"]++;
                            else if (score < 0.8) buckets["0.6-0.8"]++;
                            else buckets["0.8-1.0"]++;
                          }
                        });
                      });
                      
                      if (totalWithScores === 0) {
                        return <p className="text-sm text-muted-foreground">No confidence scores available</p>;
                      }
                      
                      return (
                        <div className="space-y-2">
                          {Object.entries(buckets).map(([range, count]) => {
                            const percentage = (count / totalWithScores) * 100;
                            const isHighConfidence = range === "0.8-1.0";
                            
                            return (
                              <div key={range} className="space-y-1">
                                <div className="flex items-center justify-between">
                                  <span className="text-sm font-medium">{range}</span>
                                  <span className="text-sm text-muted-foreground">{count} ({percentage.toFixed(1)}%)</span>
                                </div>
                                <div className="h-2 w-full bg-secondary overflow-hidden rounded-full">
                                  <div 
                                    className={`h-full ${isHighConfidence ? 'bg-green-500' : 'bg-blue-500'}`} 
                                    style={{ width: `${percentage}%` }}
                                  />
                                </div>
                              </div>
                            );
                          })}
                        </div>
                      );
                    })()}
                  </CardContent>
                </Card>
                
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle>Attack Strategies</CardTitle>
                    <CardDescription>Types of honeypot strategies used</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Strategy</TableHead>
                          <TableHead className="text-right">Occurrence</TableHead>
                          <TableHead className="text-right">Success Rate</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {(() => {
                          // Count strategies across all rounds
                          const strategies: Record<string, {count: number, successful: number}> = {};
                          roundsData.forEach(round => {
                            round.data.interactions?.forEach(interaction => {
                              if (interaction.analysis?.attack_strategy) {
                                const strategy = interaction.analysis.attack_strategy;
                                if (!strategies[strategy]) {
                                  strategies[strategy] = {count: 0, successful: 0};
                                }
                                strategies[strategy].count += 1;
                                if (interaction.analysis.attack_success || interaction.analysis.is_aligned === false) {
                                  strategies[strategy].successful += 1;
                                }
                              }
                            });
                          });
                          
                          // Display strategies
                          return Object.entries(strategies)
                            .sort((a, b) => b[1].count - a[1].count)
                            .map(([strategy, {count, successful}], i) => (
                            <TableRow key={i}>
                              <TableCell>{strategy}</TableCell>
                              <TableCell className="text-right">{count}</TableCell>
                              <TableCell className="text-right">
                                {successful > 0 ? `${((successful / count) * 100).toFixed(0)}%` : '0%'}
                              </TableCell>
                            </TableRow>
                          ));
                        })()}
                      </TableBody>
                    </Table>
                  </CardContent>
                </Card>
                
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle>Harm Categories</CardTitle>
                    <CardDescription>Types of harm in unsafe responses</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Category</TableHead>
                          <TableHead className="text-right">Occurrence</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {(() => {
                          // Count harm categories across all rounds
                          const categories: Record<string, number> = {};
                          roundsData.forEach(round => {
                            round.data.interactions?.forEach(interaction => {
                              if (interaction.analysis?.harm_categories) {
                                interaction.analysis.harm_categories.forEach((category: string) => {
                                  categories[category] = (categories[category] || 0) + 1;
                                });
                              }
                            });
                          });
                          
                          // Display categories
                          return Object.entries(categories)
                            .sort((a, b) => b[1] - a[1])
                            .map(([category, count], i) => (
                            <TableRow key={i}>
                              <TableCell>{category}</TableCell>
                              <TableCell className="text-right">{count}</TableCell>
                            </TableRow>
                          ));
                        })()}
                      </TableBody>
                    </Table>
                  </CardContent>
                </Card>
                
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle>Alignment Summary</CardTitle>
                    <CardDescription>Overview of defender alignment</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <p className="text-sm font-medium">Aligned Responses:</p>
                        <p className="text-2xl font-bold text-green-500">
                          {(() => {
                            let total = 0;
                            roundsData.forEach(round => {
                              total += round.data.interactions?.filter(i => i.analysis?.is_aligned).length || 0;
                            });
                            return total;
                          })()}
                        </p>
                      </div>
                      <div>
                        <p className="text-sm font-medium">Misaligned Responses:</p>
                        <p className="text-2xl font-bold text-red-500">
                          {(() => {
                            let total = 0;
                            roundsData.forEach(round => {
                              total += round.data.interactions?.filter(i => i.analysis?.is_aligned === false).length || 0;
                            });
                            return total;
                          })()}
                        </p>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              </div>
            </TabsContent>
            
            {mainRunData.summary && (
              <TabsContent value="analysis">
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle>Run Analysis</CardTitle>
                    <CardDescription>Overall analysis of this test run</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-4">
                      {mainRunData.summary && (
                        <div className="bg-gray-50 p-4 rounded-md">
                          <p className="text-sm font-medium mb-1">Summary:</p>
                          <p className="text-sm whitespace-pre-wrap">{mainRunData.summary}</p>
                        </div>
                      )}
                      
                      {mainRunData.vulnerabilities && (
                        <div>
                          <p className="text-sm font-medium mb-2">Detected Vulnerabilities:</p>
                          <div className="space-y-2">
                            {Array.isArray(mainRunData.vulnerabilities) && mainRunData.vulnerabilities.map((vuln: string, idx: number) => (
                              <div key={idx} className="bg-red-50 p-3 rounded-md border border-red-100">
                                <p className="text-sm">{vuln}</p>
                              </div>
                            ))}
                            {typeof mainRunData.vulnerabilities === 'string' && (
                              <div className="bg-red-50 p-3 rounded-md border border-red-100">
                                <p className="text-sm">{mainRunData.vulnerabilities}</p>
                              </div>
                            )}
                          </div>
                        </div>
                      )}
                      
                      {mainRunData.recommendations && (
                        <div>
                          <p className="text-sm font-medium mb-2">Recommendations:</p>
                          <div className="space-y-2">
                            {Array.isArray(mainRunData.recommendations) && mainRunData.recommendations.map((rec: string, idx: number) => (
                              <div key={idx} className="bg-blue-50 p-3 rounded-md border border-blue-100">
                                <p className="text-sm">{rec}</p>
                              </div>
                            ))}
                            {typeof mainRunData.recommendations === 'string' && (
                              <div className="bg-blue-50 p-3 rounded-md border border-blue-100">
                                <p className="text-sm">{mainRunData.recommendations}</p>
                              </div>
                            )}
                          </div>
                        </div>
                      )}

                      <Card>
                        <CardHeader className="pb-2">
                          <CardTitle>Detected Vulnerabilities</CardTitle>
                          <CardDescription>
                            Vulnerabilities identified in the system
                          </CardDescription>
                        </CardHeader>
                        <CardContent>
                          {mainRunData.detected_vulnerabilities?.length ? (
                            <div className="space-y-6">
                              {mainRunData.detected_vulnerabilities.map((vulnerability: Vulnerability, index: number) => (
                                <div key={index} className="border rounded-lg p-4">
                                  <div className="mb-2 flex items-center gap-2">
                                    <ShieldAlert className="h-5 w-5 text-red-500" />
                                    <h3 className="font-medium text-lg">{vulnerability.name}</h3>
                                  </div>
                                  
                                  <div className="grid grid-cols-3 gap-4 mb-3">
                                    <div>
                                      <p className="text-xs text-muted-foreground mb-1">Severity</p>
                                      <div className="flex items-center">
                                        <span className={`inline-block w-3 h-3 rounded-full mr-2 ${
                                          vulnerability.severity === 'high' ? 'bg-red-500' : 
                                          vulnerability.severity === 'medium' ? 'bg-orange-500' : 'bg-yellow-500'
                                        }`}></span>
                                        <span className="capitalize">{vulnerability.severity || 'Unknown'}</span>
                                      </div>
                                    </div>
                                    
                                    <div>
                                      <p className="text-xs text-muted-foreground mb-1">Category</p>
                                      <p>{vulnerability.category || 'Uncategorized'}</p>
                                    </div>
                                    
                                    <div>
                                      <p className="text-xs text-muted-foreground mb-1">Success Rate</p>
                                      <p>{vulnerability.success_rate ? `${(vulnerability.success_rate * 100).toFixed(1)}%` : 'N/A'}</p>
                                    </div>
                                  </div>
                                  
                                  <div className="mb-3">
                                    <p className="text-xs text-muted-foreground mb-1">Description</p>
                                    <p className="text-sm">{vulnerability.description}</p>
                                  </div>
                                  
                                  {vulnerability.examples && vulnerability.examples.length > 0 && (
                                    <div className="mb-3">
                                      <p className="text-xs text-muted-foreground mb-1">Examples</p>
                                      <div className="bg-muted rounded p-2 max-h-36 overflow-y-auto">
                                        <ul className="list-disc pl-5 text-sm space-y-1">
                                          {vulnerability.examples.map((example: string, i: number) => (
                                            <li key={i}>{example}</li>
                                          ))}
                                        </ul>
                                      </div>
                                    </div>
                                  )}
                                  
                                  {vulnerability.mitigation && (
                                    <div>
                                      <p className="text-xs text-muted-foreground mb-1">Recommended Mitigation</p>
                                      <p className="text-sm bg-muted/50 p-2 rounded">{vulnerability.mitigation}</p>
                                    </div>
                                  )}
                                </div>
                              ))}
                            </div>
                          ) : (
                            <p className="text-sm text-muted-foreground">No vulnerabilities detected</p>
                          )}
                        </CardContent>
                      </Card>
                    </div>
                  </CardContent>
                </Card>
              </TabsContent>
            )}
          </Tabs>
        </CardContent>
      </Card>
    </div>
  );
} 